import {ExportContent} from "@component/export/export-content";
import {Downloader} from "@component/export/downloaders/downloader";
import {FileExtension} from "@component/export/files/file-extension";

export class CsvDownloader extends Downloader<string> {
   private static readonly FILE_TYPE: string = "text/csv";
   private static readonly VALUE_SEPARATOR: string = ",";
   private static readonly ROW_TERMINATOR: string = "\n";

   constructor() {
      super(CsvDownloader.FILE_TYPE, FileExtension.CSV);
   }

   formatContent(content: ExportContent): string {
      return [
         this.formatAsCsvRow(content.labels),
         ...content.values.map(values => this.formatAsCsvRow(values)),
      ].join(CsvDownloader.ROW_TERMINATOR);
   }

   private formatAsCsvRow(strings: string[]): string {
      return strings.map(str => this.escape(str)).join(CsvDownloader.VALUE_SEPARATOR);
   }

   private escape(unescapedStr: string): string {
      const escapedStr: string = !!unescapedStr ? unescapedStr.replace('"', '""') : "";
      return `"${escapedStr}"`;
   }
}