import {PipeTransform} from "@angular/core";
import {Reflection} from "@util/reflection";
import {VsanUiUtils} from "@util/vsan-util";
import {BaseFilter} from "@component/datagrid/filter/base-filter";

/**
 * Used to filter VirtualObjectModel. It also searches children to see if its children contain specified objects.
 */
export class VirtualObjectFilter<VirtualObjectModel> extends BaseFilter<VirtualObjectModel> {
   private readonly CHILDREN_FIELD: string = "children";
   private static readonly STORAGE_POLICY_NAME_FIELD: string = "storagePolicy.name";
   private static readonly STORAGE_POLICY_PFTT_FIELD: string = "storagePolicy.pFtt";
   private static readonly STORAGE_POLICY_SFTT_FIELD: string = "storagePolicy.sFtt";

   constructor(private dataField: string, private pipe?: PipeTransform) {
      super();
   }

   accepts(data: VirtualObjectModel, search: string): boolean {
      if (VirtualObjectFilter.areMatching(this.getFieldValue(data), search)) {
         return true;
      }

      const children: VirtualObjectModel[] = data[this.CHILDREN_FIELD];
      if (!VsanUiUtils.isEmpty(children)) {
         return children.some(child => VirtualObjectFilter.areMatching(this.getFieldValue(child), search));
      }

      return false;
   }

   match(data: VirtualObjectModel, search: string): boolean {
      return this.accepts(data, search);
   }

   protected getFieldValue(virtualObject: VirtualObjectModel): string  {
      return VirtualObjectFilter.stringifyFieldValue(virtualObject, this.dataField, this.pipe);
   }

   static areMatching(fieldValue: string, search: string): boolean {
      if (VsanUiUtils.isEmpty(search)) {
         return true;
      }
      return !VsanUiUtils.isEmpty(fieldValue) && (fieldValue == search || fieldValue.toLowerCase().includes(search));
   }

   static stringifyFieldValue<VirtualObjectModel>(
         virtualObject: VirtualObjectModel, dataField: string, pipe?: PipeTransform): string {
      const fieldValue: any = Reflection.getFieldValue(virtualObject, dataField);

      if (pipe) {
         return pipe.transform(fieldValue);
      }

      if (!VsanUiUtils.isUnset(fieldValue)) {
         return ("" + fieldValue);
      }

      return VirtualObjectFilter.hasNotApplicableValue(dataField)
            ? VsanUiUtils.getString("vsan.na.label")
            : "";
   }

   private static hasNotApplicableValue(dataField: string): boolean {
      return dataField === VirtualObjectFilter.STORAGE_POLICY_NAME_FIELD ||
            dataField === VirtualObjectFilter.STORAGE_POLICY_PFTT_FIELD ||
            dataField === VirtualObjectFilter.STORAGE_POLICY_SFTT_FIELD;
   }
}