import {ClrDatagrid} from "@clr/angular";
import {DatagridExportDataSourceAdapter} from "@component/export/adapters/datagrid-export-data-source-adapter";
import {ExportDataExtractor} from "@component/export/export-data-extractor";
import {ExportDataSource} from "@component/export/export-data-source";
import {VirtualObjectModelWithParent} from "@component/virtual-objects-table/export/virtual-object-model-with-parent";
import {VirtualObjectsDataSourceAdapter} from "@component/virtual-objects-table/export/virtual-objects-data-source-adapter";
import {VirtualObjectsTypeUtil} from "@component/virtual-objects-table/virtual-objects-type.util";
import {ObjectHealthComplianceState} from "@generated/object-health-compliance-state";
import {VirtualObjectModel} from "@generated/virtual-object-model";
import {VsanObjectCompositeHealth} from "@generated/vsan-object-composite-health";
import {DurabilityStateLabelPipe} from "@pipe/virtual-objects/durability-state-label.pipe";
import {StringUtil} from "@util/string-util";
import {VsanHealthUtil} from "@util/vsan-health-util";
import {VsanUiUtils} from "@util/vsan-util";

export class VirtualObjectsExportConfig {

   private static readonly durabilityStateFormatter: DurabilityStateLabelPipe = new DurabilityStateLabelPipe();

   static createExportDataSource(
         datagrid: ClrDatagrid,
         isSelectionSupported: boolean,
         loadAllVirtualObjects: () => VirtualObjectModel[],
         isVirtualObjectSelected: (vo: VirtualObjectModel) => boolean,
         isChildVirtualObjectFiltered: (parent: VirtualObjectModel,
               child: VirtualObjectModel) => boolean): ExportDataSource {
      return new VirtualObjectsDataSourceAdapter(
            new DatagridExportDataSourceAdapter(datagrid),
            isSelectionSupported,
            loadAllVirtualObjects,
            isVirtualObjectSelected,
            isChildVirtualObjectFiltered);
   }

   static readonly fileName: string = "virtual_objects";

   static getVirtualObjectDataExtractors(
         isWhatIfData: boolean,
         showPolicy: boolean,
         isDurabilitySupported: boolean): ExportDataExtractor[] {
      return [
         VirtualObjectsExportConfig.parent,
         VirtualObjectsExportConfig.objectName,
         VirtualObjectsExportConfig.type,
         isWhatIfData ? VirtualObjectsExportConfig.whatIfResult : null,
         !isWhatIfData ? VirtualObjectsExportConfig.health : null,
         (isWhatIfData && isDurabilitySupported) ? VirtualObjectsExportConfig.durability : null,
         showPolicy ? VirtualObjectsExportConfig.storagePolicyName : null,
         showPolicy ? VirtualObjectsExportConfig.storagePolicyPFTT : null,
         showPolicy ? VirtualObjectsExportConfig.storagePolicySFTT : null,
         VirtualObjectsExportConfig.uuid,
      ].filter(dataExtractor => !!dataExtractor);
   }

   private static parent: ExportDataExtractor = {
      label: "vsan.monitor.cluster.virtualObjects.column.parent",
      value: (vo: VirtualObjectModelWithParent) => vo.isParent ? "" : VirtualObjectsTypeUtil.getDisplayName(vo.parent),
   };

   private static objectName: ExportDataExtractor = {
      label: "vsan.monitor.cluster.virtualObjects.column.name",
      value: (vo: VirtualObjectModelWithParent) => VirtualObjectsTypeUtil.getDisplayName(vo.self),
   };

   private static type: ExportDataExtractor = {
      label: "vsan.monitor.cluster.virtualObjects.column.type",
      value: (vo: VirtualObjectModelWithParent) => VirtualObjectsTypeUtil.getTypeLabel(vo.self.type),
   };

   private static whatIfResult: ExportDataExtractor = {
      label: "vsan.monitor.cluster.virtualObjects.column.whatIfResult",
      value: (vo: VirtualObjectModelWithParent) =>
            VirtualObjectsTypeUtil.getComplianceStatusLabel(vo.self.whatIfComplianceStatus),
   };

   private static health: ExportDataExtractor = {
      label: "vsan.monitor.cluster.virtualObjects.column.placementAndAvailabilityHealth",
      value: (vo: VirtualObjectModelWithParent) => VirtualObjectsExportConfig.stringifyHealth(vo.self),
   };

   private static durability: ExportDataExtractor = {
      label: "vsan.monitor.cluster.virtualObjects.column.durability",
      value: (vo: VirtualObjectModelWithParent) =>
            VirtualObjectsExportConfig.durabilityStateFormatter.transform(vo.self.durabilityState),
   };

   private static storagePolicyName: ExportDataExtractor = {
      label: "vsan.monitor.cluster.virtualObjects.column.storagePolicy",
      value: (vo: VirtualObjectModelWithParent) => vo.isParent
            ? ""
            : VirtualObjectsExportConfig.toString(vo.self.storagePolicy?.name, "vsan.na.label"),
   };

   private static storagePolicyPFTT: ExportDataExtractor = {
      label: "vsan.monitor.cluster.virtualObjects.column.storagePolicy.pftt",
      value: (vo: VirtualObjectModelWithParent) => vo.isParent
            ? ""
            : VirtualObjectsExportConfig.toString(vo.self.storagePolicy?.pFtt, "vsan.na.label"),
   };

   private static storagePolicySFTT: ExportDataExtractor = {
      label: "vsan.monitor.cluster.virtualObjects.column.storagePolicy.sftt",
      value: (vo: VirtualObjectModelWithParent) => vo.isParent
            ? ""
            : VirtualObjectsExportConfig.toString(vo.self.storagePolicy?.sFtt, "vsan.na.label"),
   };

   private static uuid: ExportDataExtractor = {
      label: "vsan.monitor.cluster.virtualObjects.column.uuid",
      value: (vo: VirtualObjectModelWithParent) => vo.self.uid,
   };

   private static toString(o: any, defaultVal: string = ""): string {
      return (typeof o?.toString === "function")
            ? o.toString()
            : VsanUiUtils.getString(defaultVal);
   }

   private static stringifyHealth(virtualObject: VirtualObjectModel): string {
      if (virtualObject.compositeHealth) {
         const compositeHealth: VsanObjectCompositeHealth = virtualObject.compositeHealth;
         return [
            (compositeHealth.complianceState &&
                  compositeHealth.complianceState !== ObjectHealthComplianceState.NON_COMPLIANT)
                  ? VsanHealthUtil.getVsanObjectHealthStatusText(compositeHealth.complianceState)
                  : null,
            compositeHealth.incomplianceReason
                  ? VsanHealthUtil.getVsanObjectHealthStatusText(compositeHealth.incomplianceReason)
                  : null,
            compositeHealth.rebuildState
                  ? VsanHealthUtil.getVsanObjectHealthStatusText(compositeHealth.rebuildState)
                  : null,
            compositeHealth.policyState
                  ? VsanHealthUtil.getVsanObjectHealthStatusText(compositeHealth.policyState)
                  : null,
         ]
               .filter((healthStatus: string) => !StringUtil.isEmpty(healthStatus))
               .join(";");
      } else {
         return VsanHealthUtil.getVsanObjectHealthStatusText(virtualObject.healthState);
      }
   }
}
