import {ContentChildren, Directive, ElementRef} from "@angular/core";
import {ClrDatagridRow} from "@clr/angular";
import {Logger} from "@util/logger";
import {ScrollToCurrent} from "./scroll-to-current.directive";

/**
 * A directive, applied on datagrid, which will scroll content to the first row, marked with [scroll-to-current] set
 * to true
 */
@Directive({
   selector: "[vsan-datagrid-scroll-to-row]"
})
export class DatagridScrollToRowDirective {

   private static readonly PARENT_ROW_SELECTOR: string = "clr-dg-row";
   private static readonly DETAIL_ROW_SELECTOR: string = "clr-dg-row-detail";
   private static readonly ROWS_SCROLLABLE_CONTAINER_SELECTOR: string = "div.datagrid";

   // there is no padding for row details. When scroll to it without current fix, the upper part of the text is
   // overlapped
   // on the border
   private static readonly DETAILS_SCROLL_FIX: number = -12;

   private isDetectionTriggered: boolean;

   @ContentChildren(ClrDatagridRow)
   set tableRows(rows: ClrDatagridRow[]) {
      // if table rows are reset, scroll again
      if (!rows || !rows.length) {
         this.isDetectionTriggered = false;
      }
   }

   private _markedForScrollingRows: ScrollToCurrent[];

   @ContentChildren(ScrollToCurrent, {descendants: true})
   set markedForScrollingRows(markedForScrollingRows: ScrollToCurrent[]) {
      this._markedForScrollingRows = markedForScrollingRows;
      if (!this.isDetectionTriggered && markedForScrollingRows && markedForScrollingRows.length) {
         // once detection change is triggered, do not trigger it again
         this.detectMarkedForScrollingRowsChange();
         this.isDetectionTriggered = true;
      }
   }

   private previousMarkedForScrollingRowsCount: number = 0;

   constructor(private el:ElementRef<HTMLElement>) {}

   /**
    * Detect if marked for scrolling rows count is changed (new rows are added). If so, postpone check.
    */
   private detectMarkedForScrollingRowsChange(): void {
      if (this.previousMarkedForScrollingRowsCount === this._markedForScrollingRows.length) {
         // no new rows added - the view is fully rendered. Time to scroll.
         const scrollToElement: ScrollToCurrent = this.findFirstRowToScrollTo();
         if (!scrollToElement) {
            return;
         }

         this.scrollToRow(scrollToElement.element);
      } else {
         // postpone check
         this.previousMarkedForScrollingRowsCount = this._markedForScrollingRows.length;
         setTimeout(() => this.detectMarkedForScrollingRowsChange());
      }
   }

   private findFirstRowToScrollTo() {
      return this._markedForScrollingRows.find(item => item.isSelectedElement);
   }

   private scrollToRow(element: ElementRef<Element>): void {
         const scrollableWrapper: Element = this.el.nativeElement.querySelector(
               DatagridScrollToRowDirective.ROWS_SCROLLABLE_CONTAINER_SELECTOR);
         scrollableWrapper.scrollTop = this.getScrollToPosition(element.nativeElement as HTMLElement);
   }

   private getScrollToPosition(row: HTMLElement): number {
      if (row.tagName.toLowerCase() === DatagridScrollToRowDirective.DETAIL_ROW_SELECTOR.toLowerCase()) {
         // scroll to previous element bottom.
         return (row.previousElementSibling as HTMLElement).offsetTop + DatagridScrollToRowDirective.DETAILS_SCROLL_FIX;
      }

      if (row.tagName.toLowerCase() === DatagridScrollToRowDirective.PARENT_ROW_SELECTOR.toLowerCase()) {
         // Parent row always contains at least one child element. Scroll to it.
         const refElement:HTMLElement = row.firstElementChild as HTMLElement;
         return refElement.offsetTop - refElement.offsetHeight;
      }

      Logger.warn("getScrollToPosition for " + row.tagName + " is not handled.");
      return 0;
   }

}