import {ClrDatagrid} from "@clr/angular";
import {Selection} from "@clr/angular/data/datagrid/providers/selection";
import {SelectionType} from "@clr/angular/data/datagrid/enums/selection-type";
import {ExportDataSource} from "@component/export/export-data-source";

export class DatagridExportDataSourceAdapter implements ExportDataSource {
   readonly DATAGRID_ALL_ITEMS_PROP: string = "_all";
   readonly DATAGRID_FILTERED_ITEMS_PROP: string = "_filtered";

   constructor(private datagrid: ClrDatagrid) { }

   get allItems(): any[] {
      return this.datagrid.items[this.DATAGRID_ALL_ITEMS_PROP] || [];
   }

   get allItemsCount(): number {
      return this.allItems.length;
   }

   get filteredItems(): any[] {
      return this.datagrid.items[this.DATAGRID_FILTERED_ITEMS_PROP] || [];
   }

   get filteredItemsCount(): number {
      return this.filteredItems.length;
   }

   get isSelectionEnabled(): boolean {
      return this.dgSelection.selectionType !== this.dgSelectionType.None;
   }

   get selectedItems(): any[] {
      if (this.dgSelection.selectionType === this.dgSelectionType.Multi) {
         return this.dgSelection.current || [];
      } else if (this.dgSelection.selectionType === this.dgSelectionType.Single && this.dgSelection.currentSingle) {
         return [this.dgSelection.currentSingle];
      } else {
         return [];
      }
   }

   get selectedItemsCount(): number {
      return this.selectedItems.length;
   }

   private get dgSelection(): Selection {
      return this.datagrid.selection;
   }

   private get dgSelectionType(): typeof SelectionType {
      return this.datagrid.SELECTION_TYPE;
   }
}