import {Component, EventEmitter, Input, OnChanges, Output, SimpleChanges} from "@angular/core";
import {EnumeratedFilterSelectionResolver} from "@component/datagrid/filter/enumerated-filter.component";
import {VsanUiUtils} from "@util/vsan-util";
import {KeyValue} from "@angular/common";

@Component({
   templateUrl: "enum-filter.html",
   selector: "vsan-enum-filter",
   styleUrls: ["enum-filter.scss"],
})
export class EnumFilterComponent<T, K> implements OnChanges {

   objectGroups: Map<T, number>;
   filterSelectionResolver: EnumeratedFilterSelectionResolver<T, K>;

   @Input()
   objects: K[];

   private _field: string;

   get field(): string {
      return this._field;
   }

   @Input()
   set field(field: string) {
      this._field = field;
      this.filterSelectionResolver = this.createFilterSelectionResolver(field);
   }

   @Input()
   selectedFilterValues: K[];

   @Output()
   selectedFilterValuesChange: EventEmitter<K[]> = new EventEmitter<K[]>();

   @Input()
   enumToLabelConverter: EnumToLabelConverter<T>;

   ngOnChanges(changes: SimpleChanges): void {
      // If etiher "objects" or "field" props are changed, the groups map needs to be rebuilt
      if (changes.objects && changes.objects.currentValue ||
            changes.field && changes.field.currentValue) {
         this.objectGroups = this.generateGroupsMap();
      }
   }

   private generateGroupsMap(): Map<any, number> {
      const groups: Map<K, number> = this.groupObjectsByField(this.objects);
      if (!groups || !groups.size) {
         return groups;
      }
      // filter empty types, sort alphabetically. a[0], b[0] compare keys; a[1], b[1] compare values
      return new Map([...groups.entries()]
            .sort((a, b) => a[0].toString().localeCompare(b[0].toString())));
   }

   /**
    * Creates a FilterSelectionResolver that is used when a filter is applied.
    */
   private createFilterSelectionResolver(prop: string): EnumeratedFilterSelectionResolver<T, K> {
      return new class extends EnumeratedFilterSelectionResolver<T, K> {

         constructor(private field: string) {
            super();
         }

         isSelected(item: any, filters: any[]): boolean {
            // If the given object matches the filter, show it
            if (filters.some(filter => (filter === item[this.field]))) {
               return true;
            }
            // If any of the object's children matches the filter, show the whole object (not only the child)
            const children: any[] = item.children;
            if (!VsanUiUtils.isEmpty(children) && filters.some(
                  filter => children.some(child => child[this.field] === filter))) {
               return true;
            }
            // Otherwise - hide the object
            return false;
         }
      }(prop);
   }

   /**
    * Returns the label to be shown in the filter's dropdown.
    */
   getLabel(type: KeyValue<T, number>): string {
      return `${this.enumToLabelConverter(type.key)} (${type.value})`;
   }

   /**
    * Recursively traverse all the objects and their children and groups them by the filter field. Then returns
    * a report about how many objects each group has.
    * @param objects The objects to be traversed
    * @param groups The initial map holding the groups
    */
   private groupObjectsByField(objects: any[],
         groups: Map<any, number> = new Map<any, number>(),
         childrenProp: string = "children"): Map<any, number> {
      objects.forEach(obj => {
         const key: any = obj[this.field];
         if (!!key) {
            if (!groups.has(key)) {
               groups.set(key, 0);
            }
            let count: number = groups.get(key);
            groups.set(key, ++count);
         }

         // recursively traverse all children if any
         const children: any[] = obj[childrenProp];
         if (children && children.length) {
            this.groupObjectsByField(children, groups);
         }
      });
      return groups;
   }

   onSelectedFilterValuesChange(value: K[]): void {
      if (this.selectedFilterValues === value) {
         return;
      }
      this.selectedFilterValues = value;
      this.selectedFilterValuesChange.emit(this.selectedFilterValues);
   }
}

/**
 * A mapper function between an enum and a string that will be shown in the UI.
 */
export type EnumToLabelConverter<T> = (type: T) => string;
