import {ExportDataExtractor} from "@component/export/export-data-extractor";
import {VsanUiUtils} from "@util/vsan-util";

export class ExportContent {
   /**
    * Content in a generic format that we can use to export in different formats (CSV, XLSX, etc.)
    * @param labels All labels
    * @param values Each element is array of values for the corresponding label
    */
   constructor(public labels: string[], public values: string[][]) { }

   static create(items: any[], dataExtractors: ExportDataExtractor[]): ExportContent {
      const labels: string[] = ExportContent.getLabels(dataExtractors);
      const values: string[][] = items.map(item => ExportContent.getValues(dataExtractors, item));
      return new ExportContent(labels, values);
   }

   private static getLabels(dataExtractors: ExportDataExtractor[]): string[] {
      return dataExtractors.map(dataExtractor => {
         const label: string = (typeof dataExtractor.label === "string") ? dataExtractor.label : dataExtractor.label();
         return VsanUiUtils.getString(label);
      });
   }

   private static getValues(dataExtractors: ExportDataExtractor[], item: any): string[] {
      return dataExtractors.map(dataExtractor => dataExtractor.value(item) || "");
   }
}