import {Component, EventEmitter, Input, Output} from "@angular/core";
import {ExportDataSource} from "@component/export/export-data-source";
import {ExportDataExtractor} from "@component/export/export-data-extractor";
import {ExportContent} from "@component/export/export-content";
import {CsvDownloader} from "@component/export/downloaders/csv-downloader";
import {Downloader} from "@component/export/downloaders/downloader";

@Component({
   selector: "vsan-export",
   templateUrl: "export.component.html",
   styleUrls: ["export.component.scss"],
})
export class ExportComponent {

   private downloader: Downloader<any> = new CsvDownloader();

   @Input()
   filename: string;

   @Input()
   source: ExportDataSource;

   @Input()
   dataExtractors: ExportDataExtractor[];

   @Output()
   onLoadingStateChange: EventEmitter<boolean> = new EventEmitter<boolean>();

   @Output()
   onLoadingError: EventEmitter<string> = new EventEmitter<string>();

   isLoading: boolean = false;

   allItemsCount: number = 0;
   filteredItemsCount: number = 0;
   selectedItemsCount: number = 0;

   /**
    * Keep all counts locally because if the ExportDataSource implementation use getters they will be evaluated multiple times.
    * The drawback is that if those counts change while the dropdown is open, we won't dynamically update them.
    * However we don't expect such scenarios, because if someone click on something else to filter or select
    * the items, we will close the dropdown and next time they will be the correct ones.
    */
   open(): void {
      this.allItemsCount = this.source?.allItemsCount || 0;
      this.filteredItemsCount = this.source?.filteredItemsCount || 0;
      this.selectedItemsCount = this.source?.isSelectionEnabled ? (this.source?.selectedItemsCount || 0) : 0;
   }

   getFileName(downloadingType: string): string {
      return `${this.filename}_${downloadingType}`;
   }

   export(items: Promise<any[]> | any[], fileName: string): void {
      if (!this.dataExtractors?.length) {
         return;
      }

      if (items instanceof Promise) {
         this.changeLoadingState(true);
         items.then((loadedItems: any[]) => this.download(loadedItems, fileName))
              .catch(error => this.onLoadingError.emit(error))
              .finally(() => this.changeLoadingState(false));
      } else {
         this.download(items, fileName);
      }
   }

   private changeLoadingState(isLoading: boolean): void {
      this.isLoading = isLoading;
      this.onLoadingStateChange.emit(isLoading);
   }

   private download(items: any[], fileName: string): void {
      if (!items?.length) {
         return;
      }

      this.downloader.download(ExportContent.create(items, this.dataExtractors), fileName);
   }
}