import {Component, EventEmitter, Input, OnInit, Output, QueryList, ViewChild, ViewChildren} from "@angular/core";
import {ClrDatagrid, ClrDatagridPagination, ClrDatagridRow as DatagridRow} from "@clr/angular";
import {ExportDataSource} from "@component/export/export-data-source";
import {VirtualObjectFilter} from "@component/virtual-objects-table/filter/virtual-object-filter";
import {VirtualObjectsTableStateService} from "@component/virtual-objects-table/virtual-objects-table-state.service";
import {VirtualObjectsTableService} from "@component/virtual-objects-table/virtual-objects-table.service";
import {VirtualObjectsTypeUtil} from "@component/virtual-objects-table/virtual-objects-type.util";
import {VirtualObjectWhatIfStateLabelPipe} from "@component/virtual-objects-table/whatIf-state-label.pipe";
import {DisplayObjectType} from "@generated/display-object-type";
import {DurabilityState} from "@generated/durability-state";
import {PermissionService} from "@generated/permission-service";
import {Privileges} from "@generated/privileges";
import {VirtualObjectModel} from "@generated/virtual-object-model";
import {VirtualObjectType} from "@generated/virtual-object-type";
import {VsanObjectType} from "@generated/vsan-object-type";
import {IscsiContext} from "@navigation/context/iscsi-context";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {ManagedObject} from "@service/managed-object";
import {Icon} from "@util/icon";
import {ReferenceWatcher} from "@util/reference-watcher";
import {BaseHealthState} from "@util/vsan-health-util";
import {VsanUiUtils} from "@util/vsan-util";
import {Subject, Subscription} from "rxjs";
import {VirtualObjectsExportConfig} from "./export/virtual-objects-export-config";

/* tslint:disable:no-string-literal */
@Component({
   selector: "vsan-virtual-objects-table",
   templateUrl: "virtual-objects-table.html",
   styleUrls: ["virtual-objects-table.scss"],
   providers: [ReferenceWatcher],
})
export class VirtualObjectsTableComponent implements OnInit {
   VirtualObjectsExportConfig: typeof VirtualObjectsExportConfig = VirtualObjectsExportConfig;
   VsanUiUtils: typeof VsanUiUtils = VsanUiUtils;
   Icon: typeof Icon = Icon;
   VirtualObjectsTypeUtil: typeof VirtualObjectsTypeUtil = VirtualObjectsTypeUtil;
   readonly pageSize: number = VsanUiUtils.defaultGridPageSize;

   private readonly NAME_FIELD: string = "name";
   private readonly WHAT_IF_COMPLIANCE_FIELD: string = "whatIfComplianceStatus";
   private readonly STORAGE_POLICY_NAME_FIELD: string = "storagePolicy.name";
   private readonly STORAGE_POLICY_PFTT_FIELD: string = "storagePolicy.pFtt";
   private readonly STORAGE_POLICY_SFTT_FIELD: string = "storagePolicy.sFtt";
   private readonly UID_FIELD: string = "uid";
   // todo georgig: add KB article id when provided. Bug 2676152
   readonly ENHANCED_DURABILITY_KB: string = "";

   private readonly EXPANSION_STATE_SHOW_HIDE_CLASS: string = "expand-show-hide";
   private readonly EXPANSION_STATE_CLASS: string = "expanded";

   currentPage: number = 1;

   // Whether to show the storage policies column. Can be false if the user lacks permission to read the policies
   showPolicyColumns: boolean = false;

   private hasAppliedFilters: boolean;

   // holds subscribtion for auto expand of each row. Used to manage subscriptions
   private autoExpandRowSubscriptions: Map<string, Subscription> = new Map<string, Subscription>();
   private autoExpandRowSubject: Subject<boolean> = new Subject<boolean>();

   @ViewChild("dataGrid")
   datagrid: ClrDatagrid;

   exportDataSource: ExportDataSource;

   @ViewChild("pagination", {static: true})
   pagination: ClrDatagridPagination;

   @Input()
   loading: boolean;

   @Input()
   clusterId: string;

   // flag, will be true if the view is nested in the Pre-check evacuation dialog
   @Input()
   isWhatIfData: boolean;

   @Input()
   isDurabilitySupported: boolean;

   @Input()
   hideRowSelection: boolean;

   @Input()
   presentVirtualObjectUuids: string[];

   @Input()
   nameFilterValue: string;

   @Input()
   typeFilterValue: VirtualObjectType[];

   private _appliedHealthStateFilters: BaseHealthState[];
   private hasChangedHealthStateFilters: boolean;

   @Input()
   set appliedHealthStateFilters(value: BaseHealthState[]) {
      if (JSON.stringify(value) === JSON.stringify(this._appliedHealthStateFilters)) {
         this.hasChangedHealthStateFilters = false;
         return;
      }

      this._appliedHealthStateFilters = value;
      this.hasChangedHealthStateFilters = true;
      this.notifyFilterChange();
   }

   get appliedHealthStateFilters(): BaseHealthState[] {
      return this._appliedHealthStateFilters;
   }

   @Output()
   selectionChanged: EventEmitter<VirtualObjectModel[]> = new EventEmitter<VirtualObjectModel[]>();

   @Output()
   filterChanged: EventEmitter<boolean> = new EventEmitter<boolean>();

   private _virtualObjects: VirtualObjectModel[] = [];

   @Input()
   allVirtualObjects: VirtualObjectModel[] = [];

   get virtualObjects(): VirtualObjectModel[] {
      return this._virtualObjects;
   }

   @Input()
   set virtualObjects(value: VirtualObjectModel[]) {
      const isLastVirtualObjectsDataEmpty: boolean = VsanUiUtils.isEmpty(this._virtualObjects);
      this._virtualObjects = value ? JSON.parse(JSON.stringify(value)) : [];
      if (VsanUiUtils.isEmpty(this._virtualObjects)) {
         this.prepareSnapShotSelection();
      } else {
         this._virtualObjects.forEach((virtualObject: VirtualObjectModel) => {
            if (this.selectionSnapshot.get(virtualObject.uid)) {
               this.tableState.toggleSelection(virtualObject, true);

               if (this.hasAppliedFilters) {
                  this.tableState.toggleAutoExpand(virtualObject, true);
               }
            }
            if (virtualObject.children) {
               virtualObject.children.forEach(child => {
                  if (this.selectionSnapshot.get(child.uid)) {
                     this.tableState.toggleSelection(child, true);
                     // Expand parent item
                     this.tableState.toggleAutoExpand(virtualObject, true);
                  }
               });
            }
         });

         this.selectionSnapshot.clear();

         // If the data was empty till now, try to go to the first selection page, if any
         if (isLastVirtualObjectsDataEmpty) {
            this._virtualObjects.forEach((virtualObject: VirtualObjectModel) => {
               if (this.tableState.isSelected(virtualObject)) {
                  this.currentPage = Math.floor(this._virtualObjects.indexOf(virtualObject) / this.pageSize) + 1;
                  return;
               }
            });
         }
      }
      // Emit selection in order to update actions on init when there is navigation data.
      this.onSelectionChanged();
   }

   @ViewChildren(DatagridRow)
   set rows(value: QueryList<DatagridRow>) {
      this.unsubscribeRemovedRows(value);
      // Data grid rows are injected here by Angular upon creation
      this.gridRows = value;

      if (!value) {
         return;
      }

      value.forEach(row => {
         if (!row.displayCells) {
            // during datagrid calculation some rows are returned twice. Ignore them.
            return;
         }

         this.registerForAutoExpandEvent(row);

         // Angular can inject the same row several times.
         // We use a flag to avoid registering the listener twice.
         if (!row["vsanSubscription"]) {

            // Listener that saves the last state of the row in the selection object.
            // Needed to restore the rows state when user changes pages,
            // because rows get re-created when page is changed.
            this.referenceWatcher.watchSubscriptionRef(row.expandedChange.subscribe(isExpanded => {
               this.tableState.toggleManualExpand(row.item, isExpanded);
            }));
            row["vsanSubscription"] = true;
         }

         if (this.tableState.isExpanded(row.item)) {
            setTimeout(() => {
               this.toggleRowExpansion(row);
            });
         }
      });
   }

   get rows(): QueryList<DatagridRow> {
      return this.gridRows;
   }

   complianceFilterValue: string;
   storagePolicyNameFilterValue: string;
   storagePolicyPFTTFilterValue: string;
   storagePolicySFTTFilterValue: string;
   vsanUuidFilterValue: string;

   // Filters
   nameFilter: VirtualObjectFilter<VirtualObjectModel> = new VirtualObjectFilter(this.NAME_FIELD);
   whatIfComplianceStatusFilter: VirtualObjectFilter<VirtualObjectModel> =
         new VirtualObjectFilter(this.WHAT_IF_COMPLIANCE_FIELD, new VirtualObjectWhatIfStateLabelPipe());
   storagePolicyNameFilter: VirtualObjectFilter<VirtualObjectModel> = new VirtualObjectFilter(
         this.STORAGE_POLICY_NAME_FIELD);
   storagePolicyPFTTFilter: VirtualObjectFilter<VirtualObjectModel> = new VirtualObjectFilter(
         this.STORAGE_POLICY_PFTT_FIELD);
   storagePolicySFTTFilter: VirtualObjectFilter<VirtualObjectModel> = new VirtualObjectFilter(
         this.STORAGE_POLICY_SFTT_FIELD);
   vsanUuidFilter: VirtualObjectFilter<VirtualObjectModel> = new VirtualObjectFilter(this.UID_FIELD);

   private selectionSnapshot: Map<string, boolean> = new Map<string, boolean>();
   private gridRows: QueryList<DatagridRow>;

   constructor(private permissionService: PermissionService,
         private virtualObjectsTableService: VirtualObjectsTableService,
         private navigationService: NavigationService,
         private referenceWatcher: ReferenceWatcher,
         public tableState: VirtualObjectsTableStateService) {
      this.referenceWatcher.watchSubscriptionRef(
            this.tableState.selectionChanged.subscribe(() => this.onSelectionChanged()));
   }

   onSelectionChanged = (): void => {
      this.selectionChanged.emit(this.getSelectedVirtualObjects());
   };

   async ngOnInit(): Promise<void> {
      this.prepareSnapShotSelection();
      const managedObject: ManagedObject = this.clusterId
            ? ManagedObject.fromUid(this.clusterId)
            : ManagedObject.contextObject;
      this.showPolicyColumns = await this.permissionService.hasVcPermissions(managedObject,
            [Privileges.READ_POLICIES()]);
      this.notifyFilterChange();
      this.referenceWatcher.watchSubscriptionRef(this.virtualObjectsTableService.onResetFilters.subscribe(
            () => this.clearFilters()));
      this.initExportDataSource();
   }

   private initExportDataSource(): void {
      this.exportDataSource = VirtualObjectsExportConfig.createExportDataSource(
            this.datagrid,
            !this.hideRowSelection,
            () => this.allVirtualObjects,
            (vo: VirtualObjectModel) => this.tableState.isSelected(vo),
            (parent: VirtualObjectModel, child: VirtualObjectModel) => this.isMatchingTextSearch(parent, child));
   }

   private prepareSnapShotSelection(): void {
      const selectedUuids: Map<string, boolean> = new Map<string, boolean>();
      this.getSelectedVirtualObjects().forEach(model => selectedUuids.set(model.uid, true));
      this.selectionSnapshot = selectedUuids;
      if (!VsanUiUtils.isEmpty(this.presentVirtualObjectUuids)) {
         this.presentVirtualObjectUuids.forEach(uuid => {
            this.selectionSnapshot.set(uuid, true);
         });
      }
   }

   isLink(item: VirtualObjectModel): boolean {
      return this.isVM(item)
            || this.isExtensionApp(item)
            || this.isIscsiTarget(item);
   }

   navigate(item: VirtualObjectModel): void {
      if (this.isVM(item)) {
         this.navigationService.navigateTo(PlatformExtension.VM_SUMMARY, item.vmRef);
      } else if (this.isExtensionApp(item)) {
         this.navigationService.navigateToPersistenceServiceExtension(item.type.extendedTypeId,
               item.applicationInstanceId, item.vmRef);
      } else if (this.isIscsiTarget(item)) {
         const iscsiTargetsContext: IscsiContext = {
            targetAlias: item.name,
         };
         this.navigationService.navigateTo(PlatformExtension.CLUSTER_ISCSI_TARGETS, null, iscsiTargetsContext);
      }
   }

   private registerForAutoExpandEvent(row: DatagridRow): void {
      if (this.autoExpandRowSubscriptions.has(row.id)) {
         // already registered
         return;
      }
      const expandableStateRowSubscription: Subscription = this.autoExpandRowSubject.subscribe(
            isExpanded => {
               setTimeout(() => {
                  this.tableState.toggleAutoExpand(row.item, isExpanded);
                  this.toggleRowExpansion(row);
               });
            });
      this.referenceWatcher.watchSubscriptionRef(expandableStateRowSubscription);
      this.autoExpandRowSubscriptions.set(row.id, expandableStateRowSubscription);
   }

   toggleRowExpansion = (row: DatagridRow): void => {
      // stop animation while auto expanding to avoid flickering.
      row.expand["_animate"].isStopped = true;
      row.expand.expanded = this.tableState.isExpanded(row.item);
      row.expand["_animate"].isStopped = false;
   };

   private unsubscribeRemovedRows(newRows: QueryList<DatagridRow> = new QueryList<DatagridRow>()): void {
      this.autoExpandRowSubscriptions.forEach((value, key, map) => {
         if (!newRows.find(row => row.id === key)) {
            value.unsubscribe();
            map.delete(key);
         }
      });
   }

   private clearFilters(): void {
      this.nameFilterValue = "";
      this.complianceFilterValue = "";
      this.storagePolicyNameFilterValue = "";
      this.storagePolicyPFTTFilterValue = "";
      this.storagePolicySFTTFilterValue = "";
      this.typeFilterValue = [];
      this.notifyFilterChange();
   }

   private isVM(item: VirtualObjectModel): boolean {
      return item.vmRef !== null && item.type.displayType === DisplayObjectType.VM;
   }

   private isExtensionApp(item: VirtualObjectModel): boolean {
      return item.vmRef != null
            && item.applicationInstanceId !== null
            && item.type.displayType === DisplayObjectType.EXTENSION_APP;
   }

   private isIscsiTarget(item: VirtualObjectModel): boolean {
      return item.type.vmodlType === VsanObjectType.iscsiTarget
            && item.type.displayType === DisplayObjectType.ISCSI_TARGET;
   }

   get allSelected(): boolean {
      return this.tableState.areAllSelected(this.virtualObjects);
   }

   set allSelected(value: boolean) {
      this.tableState.toggleSelectionOnAll(this.virtualObjects, value);
   }

   /**
    * Returns a flat list of all selected virtual objects
    */
   private getSelectedVirtualObjects(): VirtualObjectModel[] {
      return this.tableState.getSelectedVirtualObjects(
            this.virtualObjects,
            (parent: VirtualObjectModel, child: VirtualObjectModel) => this.isMatchingTextSearch(parent, child));
   }

   isContainerItem(item: VirtualObjectModel): boolean {
      return !item.uid;
   }

   isMatchingTextSearch(parent: VirtualObjectModel, child: VirtualObjectModel): boolean {
      if (this.nameFilterValue) {
         return this.containSearch(parent, child, this.NAME_FIELD, this.nameFilterValue);
      }
      if (this.complianceFilterValue) {
         return this.containSearch(parent, child, this.WHAT_IF_COMPLIANCE_FIELD, this.complianceFilterValue,
               new VirtualObjectWhatIfStateLabelPipe());
      }
      if (this.storagePolicyNameFilterValue) {
         return this.containSearch(parent, child, this.STORAGE_POLICY_NAME_FIELD, this.storagePolicyNameFilterValue);
      }
      if (this.storagePolicyPFTTFilterValue) {
         return this.containSearch(parent, child, this.STORAGE_POLICY_PFTT_FIELD, this.storagePolicyPFTTFilterValue);
      }
      if (this.storagePolicySFTTFilterValue) {
         return this.containSearch(parent, child, this.STORAGE_POLICY_SFTT_FIELD, this.storagePolicySFTTFilterValue);
      }
      if (this.vsanUuidFilterValue) {
         return this.containSearch(parent, child, this.UID_FIELD, this.vsanUuidFilterValue);
      }
      return true;
   }

   private containSearch(
         parent: VirtualObjectModel, child: VirtualObjectModel, dataField: string, search: any, pipe?: any): boolean {
      return (parent && this.match(parent, dataField, search, pipe)) ||
            (child && this.match(child, dataField, search, pipe));
   }

   private match(virtualObject: VirtualObjectModel, dataField: string, search: any, pipe?: any): boolean {
      const fieldValue: string = VirtualObjectFilter.stringifyFieldValue(virtualObject, dataField, pipe);
      return VirtualObjectFilter.areMatching(fieldValue, search);
   }

   onPolicyClicked = () => {
      this.navigationService.navigateWithoutContextObject(PlatformExtension.VM_STORAGE_POLICIES_VIEW);
   };

   onSelectedFilterTypeChanged(value: VirtualObjectType[]): void {
      this.typeFilterValue = value;
      this.notifyFilterChange();
   }

   notifyFilterChange(): void {
      // We must invoke onSelectionChanged because otherwise we will work with the old selection
      this.onSelectionChanged();
      const hasAppliedFilters: boolean = !VsanUiUtils.isEmpty(this.appliedHealthStateFilters) ||
            !VsanUiUtils.isEmpty(this.nameFilterValue) ||
            !VsanUiUtils.isEmpty(this.complianceFilterValue) ||
            !VsanUiUtils.isEmpty(this.storagePolicyNameFilterValue) ||
            !VsanUiUtils.isEmpty(this.storagePolicyPFTTFilterValue) ||
            !VsanUiUtils.isEmpty(this.storagePolicySFTTFilterValue) ||
            !VsanUiUtils.isEmpty(this.vsanUuidFilterValue) ||
            !VsanUiUtils.isEmpty(this.typeFilterValue);
      if (this.hasAppliedFilters === hasAppliedFilters && !this.hasChangedHealthStateFilters) {
         return;
      }
      this.hasAppliedFilters = hasAppliedFilters;

      this.filterChanged.emit(hasAppliedFilters);
      setTimeout(() => {
         this.autoExpandRowSubject.next(hasAppliedFilters);
      });
   }

   getDatagridFooterLabel(): string {
      return VsanUiUtils.getDatagridFooterLabel(this.virtualObjects, this.pagination, this.pageSize);
   }

   getTypeLabel(type: VirtualObjectType): string {
      return VirtualObjectsTypeUtil.getTypeLabel(type);
   }

   expandShowHideAggregationStyle(item: VirtualObjectModel): string[] {
      const resultStyle: string[] = [];
      // apply the class if the row expansion state should switch visibility
      if (!item.uid) {
         resultStyle.push(this.EXPANSION_STATE_SHOW_HIDE_CLASS);
      } else {
         // without marker class no need to set actual state
         return resultStyle;
      }
      // apply expand state
      if (this.tableState.isExpanded(item)) {
         resultStyle.push(this.EXPANSION_STATE_CLASS);
      }
      return resultStyle;
   }

   getDurabilityLabel(durabilityState: DurabilityState): string {
      switch (durabilityState) {
         case DurabilityState.GUARANTEED:
            return VsanUiUtils.getString("vsan.durability.guaranteed");
         case DurabilityState.NO_RESOURCE:
            return VsanUiUtils.getString("vsan.durability.notGuaranteed.no.resource");
         case DurabilityState.EXCEEDED_COMP_LIMIT:
            return VsanUiUtils.getString("vsan.durability.notGuaranteed.exeeded.comp.limit");
         case DurabilityState.NO_SPACE:
            return VsanUiUtils.getString("vsan.durability.notGuaranteed.no.space");
         case DurabilityState.STALE_DURABILITY_COMP:
            return VsanUiUtils.getString("vsan.durability.notGuaranteed.stale.durability.space");
         case DurabilityState.UNKNOWN:
         default:
            return VsanUiUtils.getString("vsan.durability.unknown");
      }
   }
}