import {ExportContent} from "@component/export/export-content";
import {FileUtils} from "@component/export/files/file-utils";

export abstract class Downloader<TContentType> {

   abstract formatContent(content: ExportContent): TContentType;

   protected constructor(private fileType: string, private fileExtension: string) { }

   download(content: ExportContent, fileName: string): void {
      FileUtils.download(
            this.createBlob(content),
            this.buildFileNameWithExtension(fileName, true));
   }

   createBlob(content: ExportContent): Blob {
      return FileUtils.createBlob(this.formatContent(content), this.fileType);
   }

   buildFileNameWithExtension(fileName: string, addTimeStamp: boolean = false): string {
      return FileUtils.buildFileNameWithExtension(fileName, this.fileExtension, addTimeStamp);
   }
}