import {ExportDataSource} from "@component/export/export-data-source";
import {VirtualObjectModel} from "@generated/virtual-object-model";
import {VirtualObjectModelWithParent} from "@component/virtual-objects-table/export/virtual-object-model-with-parent";
import {DatagridExportDataSourceAdapter} from "@component/export/adapters/datagrid-export-data-source-adapter";

export class VirtualObjectsDataSourceAdapter implements ExportDataSource {
   constructor(
         private datagridAdapter: DatagridExportDataSourceAdapter,
         private isSelectionSupported: boolean,
         private loadAllVirtualObjects: () => VirtualObjectModel[],
         private isVirtualObjectSelected: (vo: VirtualObjectModel) => boolean,
         private isChildVirtualObjectFiltered: (parent: VirtualObjectModel, child: VirtualObjectModel) => boolean) { }

   get allItems(): any[] {
      const allVirtualObjects: VirtualObjectModel[] = this.loadAllVirtualObjects();
      return allVirtualObjects?.length > 0
            ? this.flat(allVirtualObjects)
            : this.flat(this.allVirtualObjectsByDatagrid);
   }

   get allItemsCount(): number {
      return this.allItems.length;
   }

   get filteredItems(): any[] {
      return this.flat(this.filteredVirtualObjectsByDatagrid).filter(vo => this.isParentOrFilteredChild(vo));
   }

   get filteredItemsCount(): number {
      return this.filteredItems.length;
   }

   get isSelectionEnabled(): boolean {
      return this.isSelectionSupported;
   }

   get selectedItems(): any[] {
      return (this.filteredItems as VirtualObjectModelWithParent[]).filter(vo => this.isVirtualObjectSelected(vo.self));
   }

   get selectedItemsCount(): number {
      return this.selectedItems.length;
   }

   private flat(virtualObjects: VirtualObjectModel[]): VirtualObjectModelWithParent[] {
      if (!virtualObjects?.length) {
         return [];
      }

      return virtualObjects.reduce(
            (accumulator: VirtualObjectModelWithParent[], virtualObject: VirtualObjectModel) => accumulator
                  .concat(VirtualObjectModelWithParent.createParent(virtualObject))
                  .concat(...VirtualObjectModelWithParent.createChildren(virtualObject, virtualObject.children))
            , []);
   }

   private get allVirtualObjectsByDatagrid(): VirtualObjectModel[] {
      return this.datagridAdapter.allItems as VirtualObjectModel[];
   }

   private get filteredVirtualObjectsByDatagrid(): VirtualObjectModel[] {
      return this.datagridAdapter.filteredItems as VirtualObjectModel[];
   }

   /**
    * Parent virtual objects are already filtered by the datagrid filters.
    * We need to filter child virtual objects because they are manually handled in the virtual objects table.
    */
   private isParentOrFilteredChild(virtualObject: VirtualObjectModelWithParent): boolean {
      return virtualObject.isParent || this.isChildVirtualObjectFiltered(virtualObject.parent, virtualObject.self);
   }
}