import {Directive, ElementRef, Input} from "@angular/core";

/**
 * Marks a datagrid row. The content of the datagrid with applied [vsan-datagrid-scroll] directive will scroll
 * to the first row with directive set to true
 */
@Directive({
   selector: "[vsan-scroll-to-current]"
})
export class ScrollToCurrent {

   @Input("vsan-scroll-to-current")
   isSelectedElement: boolean;

   get element(): ElementRef<Element> {
      return this.el;
   }

   constructor(private el: ElementRef<Element>) {}
}
