import {Pipe, PipeTransform} from "@angular/core";
import {VirtualObjectsTypeUtil} from "@component/virtual-objects-table/virtual-objects-type.util";
import {VsanWhatIfComplianceStatus} from "@generated/vsan-what-if-compliance-status";

@Pipe({
   name: "whatIfStateLabel",
})
export class VirtualObjectWhatIfStateLabelPipe implements PipeTransform {

   transform(status: VsanWhatIfComplianceStatus): string {
      return VirtualObjectsTypeUtil.getComplianceStatusLabel(status);
   }
}