import {VirtualObjectModel} from "@generated/virtual-object-model";
import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {VirtualObjectType} from "@generated/virtual-object-type";
import {EnumeratedFilterSelectionResolver} from "@component/datagrid/filter/enumerated-filter.component";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "virtual-objects-type-filter.html",
   selector: "vsan-virtual-objects-type-filter",
})
export class VirtualObjectsTypeFilterComponent implements OnInit {

   readonly FIELD_NAME: string = "type";

   filterItems: FilterTypeItem[] = [];

   filterSelectionResolver: EnumeratedFilterSelectionResolver<VirtualObjectModel, VirtualObjectType> =
         this.createFilterSelectionResolver(this.FIELD_NAME);

   @Input()
   labelFunction: TypeFilterLabelResolver;

   private _virtualObjects: VirtualObjectModel[];

   @Input()
   set virtualObjects(objects: VirtualObjectModel[]) {
      this._virtualObjects = objects;
      if (this.isInit) {
         this.setFilterItems(objects);
      }
   }

   get virtualObjects(): VirtualObjectModel[] {
      return this._virtualObjects;
   }

   @Input()
   selectedFilterValues: VirtualObjectType[];

   @Output()
   selectedFilterValuesChange: EventEmitter<VirtualObjectType[]> = new EventEmitter<VirtualObjectType[]>();

   private isInit: boolean = false;

   ngOnInit(): void {
      this.setFilterItems(this.virtualObjects);
      this.isInit = true;
   }

   onSelectedFilterValuesChange(value: VirtualObjectType[]): void {
      this.selectedFilterValues = value;
      this.selectedFilterValuesChange.emit(this.selectedFilterValues);
   }

   getTypeFilterLabel(type: FilterTypeItem): string {
      return `${this.labelFunction(type.filter)} ${type.count}`;
   }

   private setFilterItems(objects: VirtualObjectModel[]): void {
      this.filterItems = [];
      if (VsanUiUtils.isEmpty(objects)) {
         return;
      }

      // loop only through root virtual objects. We need only their types in the filter.
      objects.forEach(obj => {
         const filterItem: FilterTypeItem = this.findFilter(obj.type);
         if (filterItem) {
            filterItem.count++;
         } else {
            this.filterItems.push(new FilterTypeItem(obj.type));
         }
      });
      this.filterItems.sort((a, b) => {
         // descending sort by count
         const countCompare: number = b.count - a.count;
         if (countCompare !== 0) {
            return countCompare;
         }
         // if counts are equal ascending sort by name
         return this.labelFunction(a.filter).localeCompare(this.labelFunction(b.filter));
      });
   }

   /**
    * Creates a FilterSelectionResolver that is used when a filter is applied.
    */
   private createFilterSelectionResolver(prop: string):
         EnumeratedFilterSelectionResolver<VirtualObjectModel, VirtualObjectType> {
      return new class extends EnumeratedFilterSelectionResolver<VirtualObjectModel, VirtualObjectType> {

         constructor(private field: string) {
            super();
         }

         // only root objects are passed to the filter. For this filter we filter only by them - no need to loop
         // through the children
         isSelected(item: VirtualObjectModel, filters: VirtualObjectType[]): boolean {
            // If the given object matches the filter, show it
            if (filters.some(filter => this.areEquals(filter, item[this.field]))) {
               return true;
            }
            // Otherwise - hide the object
            return false;
         }

         areEquals(filterValue1: VirtualObjectType, filterValue2: VirtualObjectType): boolean {
            if (!filterValue1.extendedTypeId && !filterValue2.extendedTypeId) {
               return filterValue1.displayType === filterValue2.displayType;
            }
            return filterValue1.extendedTypeId === filterValue2.extendedTypeId;
         }
      }(prop);
   }

   private findFilter(type: VirtualObjectType): FilterTypeItem {
      return this.filterItems.find(value => this.filterSelectionResolver.areEquals(type, value.filter));
   }
}

export class FilterTypeItem {
   constructor(public filter: VirtualObjectType, public count: number = 1) {
   }
}

export type TypeFilterLabelResolver = (filter: VirtualObjectType) => string;
