import {Pipe, PipeTransform} from "@angular/core";
import {VsanWhatIfComplianceStatus} from "@generated/vsan-what-if-compliance-status";
import {Icon} from "@util/icon";
import {Logger} from "@util/logger";

@Pipe({
   name: 'whatIfStateIconShape'
})
export class VirtualObjectWhatIfStateIconShapePipe implements PipeTransform {

   public transform(state: VsanWhatIfComplianceStatus): string {
      switch (state) {
         case VsanWhatIfComplianceStatus.INACCESSIBLE:
            return Icon.CLR_ERROR_STANDARD_ICON_SHAPE;
         case VsanWhatIfComplianceStatus.NOT_COMPLIANT:
            return Icon.CLR_WARN_STANDARD_ICON_SHAPE;
         default:
            Logger.warn("Unknown VsanWhatIfComplianceStatus status: " + status);
            return Icon.CLR_INFO_STANDARD_ICON_SHAPE;
      }
   }
}