import {Pipe} from "@angular/core";
import {DurabilityState} from "@generated/durability-state";
import {Icon} from "@util/icon";

@Pipe({
   name: "durabilityStateIconShape",
})
export class DurabilityStateIconShapePipe {
   transform(state: DurabilityState): string {
      switch (state) {
         case DurabilityState.GUARANTEED:
            return Icon.CLR_SUCCESS_STANDARD_ICON_SHAPE;
         case DurabilityState.EXCEEDED_COMP_LIMIT:
         case DurabilityState.NO_RESOURCE:
         case DurabilityState.NO_SPACE:
         case DurabilityState.STALE_DURABILITY_COMP:
            return Icon.CLR_WARN_STANDARD_ICON_SHAPE;
         default:
            return Icon.EMPTY_ICON;
      }
   }
}