import {VirtualObjectModel} from "@generated/virtual-object-model";

export class VirtualObjectModelWithParent {
   constructor(
         public parent: VirtualObjectModel,
         public self: VirtualObjectModel) { }

   get isParent(): boolean {
      return !this.parent;
   }

   static createParent(virtualObject: VirtualObjectModel): VirtualObjectModelWithParent {
      return new VirtualObjectModelWithParent(null, virtualObject);
   }

   static createChildren(
         parentVirtualObject: VirtualObjectModel,
         childrenVirtualObjects: VirtualObjectModel[]): VirtualObjectModelWithParent[] {
      return !childrenVirtualObjects?.length
            ? []
            : childrenVirtualObjects.map(child => new VirtualObjectModelWithParent(parentVirtualObject, child));
   }
}