import {Pipe, PipeTransform} from "@angular/core";
import {VsanWhatIfComplianceStatus} from "@generated/vsan-what-if-compliance-status";
import {Icon} from "@util/icon";

@Pipe({
   name: 'whatIfStateIconClass'
})
export class VirtualObjectWhatIfStateIconClassPipe implements PipeTransform {

   public transform(state: VsanWhatIfComplianceStatus): string {
      if (!state) {
         return Icon.CLR_SOLID_ICON_CLASS;
      }
      return this.getColorClass(state) + " " + Icon.CLR_SOLID_ICON_CLASS;
   }

   private getColorClass(state: VsanWhatIfComplianceStatus): string {
      switch (state) {
         case VsanWhatIfComplianceStatus.INACCESSIBLE:
            return Icon.CLR_ERROR_ICON_CLASS;
         case VsanWhatIfComplianceStatus.NOT_COMPLIANT:
            return Icon.CLR_WARN_ICON_CLASS;
         default:
            return Icon.CLR_HIGHLIGHT_ICON_CLASS;
      }
   }
}
